body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.app-background {
  padding: 40px;
  width: 70%;
  margin: auto;
}
.header {
  display: flex;
}
.logo {
  width: 150px;
}
.title {
  display: flex;
  align-items: center;
  justify-content: center;
}
.container {
  padding: 10px 25px;
}
.container h2 {
  border-bottom: solid 3px #000;
  padding-left: 10px;
}
.item {
  width: 100%;
  margin-bottom: 10px;
  border-left: solid 3px #3100e4;
  display: flex;
  padding-left: 10px;
  font-size: 18px;
  line-height: 21px;
}
.item span:nth-child(1) {
  width: 60%;
}
.item span:nth-child(2) {
  width: 25%;
}
.button {
  width: 12%;
  border: none;
  border-radius: 5px;
  color: #fff;
}
.button-delete {
  background-color: red;
}
.button-add {
  background-color: green;
}
div.add-item {
  width: 100%;
  display: flex;
}
input.add-item-input {
  width: 55%;
  margin-right: 10px;
}
input.add-item-amount {
  width: 25%;
  margin-right: 20px;
}
